.cards {
  padding: 5rem calc((100vw - 1200px) / 2);
  background-color: #F7F7F7;
}

.cards-container {
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.modal-wrap{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.cardItem{
      max-width: 328px;
      min-width: 280px;
      height: 271px;
      background: #ffffff;
      border: 1px solid #E5E5E5;
      box-sizing: border-box;
      border-radius: 24px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
}

.cardItem h1{
    font-family: Sofia Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 27px;
    text-align: center;
    letter-spacing: 0.2px;
    color: #014F53;
    margin-top: 10px;
}

.cardItem > strong{
    font-family: Sofia Pro;
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 32px;
    text-align: center;
    color: #014F53;
}

.cardItem-btn{
    width: 328px;
    height: 80px;
    background-color: #FF574D;
    border: 1px solid #FF574D;
    border-radius: 24px;
    outline: none;
    color: #fff;
    font-weight: bold;
    font-size: 18px;
    line-height: 18px;
}

@media screen and (max-width: 768px){
  .cards-container {
     flex-direction: column;
     gap: 40px;
  }
}

@media screen and (max-width: 400px){
  .cards-container {
     flex-direction: column;
  }
  
}