.navbar {
  box-shadow: 0px 1px 0px #E5E9F2;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
  background-color: #ffffff;
  height: 90px;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  color: #242222;
  width: 80%;
}

.navbar-logo {
  justify-self: start;
  cursor: pointer;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  height: 80px;
}

.nav-links {
  color: #242222;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.nav-links:hover {
  border-bottom: 4px solid #242424;
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: #242222;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

Link{
  text-decoration: none;
}

.user-dp{
  /* width: 200px; */
  display: flex;
  gap: 20px;
  justify-content: center;
  align-items: center;
}

.user-dp-wrap{
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background-color: #cecdcd;
}

.user-dp-wrap img{
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.dp-link{
  text-decoration: none;
  color: green;
}

@media screen and (max-width: 960px) {
  body.active {
    overflow-y: hidden;
    overflow-x: hidden;
  }

  .NavbarItems {
    position: relative;
  }

  .user-dp{
    display: none;
  }

  .nav-menu {
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 90px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
    color: #fff;
  }

  .nav-links:hover {
    background-color: #fff;
    color: #242424;
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #242222;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    /* text-align: center; */
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #fff;
    /* padding: 14px 20px; */
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
}
